<template>
  <div class="Component-IntroductorioPorHora">
    <div class="un_espaciado" v-if="!internal_hora">Cargando...</div>
    <div class="un_espaciado" v-else>
      <div class="">
        <div class="" style="margin-top: 0px">
          <div class="como_una_tabla">
            <span class="como_una_celda">
              <button class="un_control una_anchura_de_20_px" v-on:click="() => goToPreviousDay()">«</button>
            </span>
            <span class="como_una_celda ancho_maximo texto_centrado rompible_por_espacios">{{ $utils.getPoliteStringFromDate(internal_hora) }}</span>
            <span class="como_una_celda">
              <button class="un_control una_anchura_de_20_px" v-on:click="() => goToNextDay()">»</button>
            </span>
          </div>
        </div>
        <div class="">
          <div class="" style="margin-top: 6px">
            <div class="como_una_tabla">
              <span class="como_una_celda">
                <button class="un_control una_anchura_de_20_px" v-on:click="() => goToPreviousHour()">«</button>
              </span>
              <span class="como_una_celda ancho_maximo texto_centrado">{{ $utils.padStart(internal_hora.getHours(), 2, "0") }}:00 - {{ $utils.padStart(internal_hora.getHours() + 1, 2, "0") }}:00</span>
              <span class="como_una_celda">
                <button class="un_control una_anchura_de_20_px" v-on:click="() => goToNextHour()">»</button>
              </span>
            </div>
          </div>
        </div>
      </div>
      <div class="texto_centrado el_mensaje_de_no_asignados" v-if="!internal_tareas_de_hora || !internal_tareas_de_hora.length">
        <p>No hay tareas asignadas a esta hora.</p>
      </div>
      <div class="un_espaciado" v-else>
        <div class="un_texto_pequenio como_una_tabla">
          <div class="como_una_fila">
            <span class="como_una_celda ancho_auto un_espaciado_horizontal">*</span>
            <span class="como_una_celda ancho_auto un_espaciado_horizontal">Tareas: </span>
            <span class="como_una_celda ancho_auto"></span>
            <span class="como_una_celda ancho_maximo"
              ><span class="un_fondo_de_total">{{ internal_tareas_de_hora_totales }} tareas para esta hora</span></span
            >
          </div>
          <div class="como_una_fila">
            <span class="como_una_celda ancho_auto un_espaciado_horizontal">✖</span>
            <span class="como_una_celda ancho_auto un_espaciado_horizontal">Pendientes:</span>
            <span class="como_una_celda ancho_auto"></span>
            <span class="como_una_celda ancho_maximo"
              ><span class="un_fondo_de_fallido" :style="{ width: ((internal_tareas_de_hora_fallidas.length / internal_tareas_de_hora_totales) * 100).toFixed(1) + '%' }"
                >{{ internal_tareas_de_hora_fallidas.length }} de {{ internal_tareas_de_hora_totales }} ({{ ((internal_tareas_de_hora_fallidas.length / internal_tareas_de_hora_totales) * 100).toFixed(1) }}%)</span
              ></span
            >
          </div>
          <div class="como_una_fila">
            <span class="como_una_celda ancho_auto un_espaciado_horizontal">✔</span>
            <span class="como_una_celda ancho_auto un_espaciado_horizontal">Hechas: </span>
            <span class="como_una_celda ancho_auto"></span>
            <span class="como_una_celda ancho_maximo"
              ><span class="un_fondo_de_completado" :style="{ width: ((internal_tareas_de_hora_completadas.length / internal_tareas_de_hora_totales) * 100).toFixed(1) + '%' }"
                >{{ internal_tareas_de_hora_completadas.length }} de {{ internal_tareas_de_hora_totales }} ({{ ((internal_tareas_de_hora_completadas.length / internal_tareas_de_hora_totales) * 100).toFixed(1) }}%)</span
              ></span
            >
          </div>
          <template v-if="internal_tareas_de_hora_objetivos_totales">
            <div class="como_una_fila">
              <span class="como_una_celda ancho_auto un_espaciado_horizontal">**</span>
              <span class="como_una_celda ancho_auto un_espaciado_horizontal">Objetivos: </span>
              <span class="como_una_celda ancho_auto"></span>
              <span class="como_una_celda ancho_maximo"
                ><span class="un_fondo_de_total">{{ internal_tareas_de_hora_objetivos_totales }} objetivos para esta hora</span></span
              >
            </div>
            <div class="como_una_fila">
              <span class="como_una_celda ancho_auto un_espaciado_horizontal">✖✖</span>
              <span class="como_una_celda ancho_auto un_espaciado_horizontal">Pendientes: </span>
              <span class="como_una_celda ancho_auto"></span>
              <span class="como_una_celda ancho_maximo"
                ><span class="un_fondo_de_fallido" :style="{ width: ((internal_tareas_de_hora_objetivos_pendientes.length / internal_tareas_de_hora_objetivos_totales) * 100).toFixed(1) + '%' }"
                  >{{ internal_tareas_de_hora_objetivos_pendientes.length }} de {{ internal_tareas_de_hora_objetivos_totales }} ({{ ((internal_tareas_de_hora_objetivos_pendientes.length / internal_tareas_de_hora_objetivos_totales) * 100).toFixed(1) }}%)</span
                ></span
              >
            </div>
            <div class="como_una_fila">
              <span class="como_una_celda ancho_auto un_espaciado_horizontal">✔✔</span>
              <span class="como_una_celda ancho_auto un_espaciado_horizontal">Cumplidos: </span>
              <span class="como_una_celda ancho_auto"></span>
              <span class="como_una_celda ancho_maximo"
                ><span class="un_fondo_de_completado" :style="{ width: ((internal_tareas_de_hora_objetivos_sumados.length / internal_tareas_de_hora_objetivos_totales) * 100).toFixed(1) + '%' }"
                  >{{ internal_tareas_de_hora_objetivos_sumados.length }} de {{ internal_tareas_de_hora_objetivos_totales }} ({{ ((internal_tareas_de_hora_objetivos_sumados.length / internal_tareas_de_hora_objetivos_totales) * 100).toFixed(1) }}%)</span
                ></span
              >
            </div>
          </template>
        </div>
        <div class="un_visor_introductorio">
          <ul class="una_lista_de_bloques">
            <li class="un_espaciado_arriba" v-for="(tareaData, tareaIndex) in internal_tareas_de_hora" v-bind:key="'introductorio-por-hora-tarea-' + tareaIndex">
              <div class="un_texto_pequenio un_espaciado">
                <span class="">{{ tareaData["está terminada"] ? "✔" : "✖" }}</span>
                <span class=""> {{ tareaData["fecha de inicio"].split(" ")[1] }} </span>
                <span class=""> {{ tareaData.nombre }}. </span>
                <span class="un_texto_muy_pequenio un_espaciado_arriba" style="display: block; text-align: right">
                  <span v-if="tareaData['está terminada']" style="white-space: nowrap">
                    <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => $db.undoTarea(tareaData.id).then(() => refreshInternalHoraData())">Deshacer</a>
                  </span>
                  <span v-else>
                    <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => $db.doTarea(tareaData.id).then(() => refreshInternalHoraData())">Hacer</a>
                  </span>
                  <span> | </span>
                  <span>
                    <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => goToTarea(tareaData)">Editar</a>
                  </span>
                  <span> | </span>
                  <span style="white-space: nowrap">
                    <a class="un_link_escrito" href="javascript:void(0)" v-on:click="() => $db.deleteTarea(tareaData.id).then(() => refreshInternalHoraData())">Eliminar</a>
                  </span>
                  <span> tarea#{{ tareaData.id }}</span>
                  <span>.</span>
                </span>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="un_texto_pequenio texto_centrado">
        <button class="un_control" v-on:click="() => goToAddTarea()">Crear nueva tarea</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "IntroductorioPorHora",
  props: {
    root: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      internal_hora: undefined,
      internal_tareas_de_hora: undefined,
      internal_tareas_de_hora_fallidas: undefined,
      internal_tareas_de_hora_pendientes: undefined,
      internal_tareas_de_hora_completadas: undefined,
      internal_tareas_de_hora_totales: undefined,
      internal_tareas_de_hora_objetivos_sumados: [],
      internal_tareas_de_hora_objetivos_pendientes: [],
      internal_tareas_de_hora_objetivos_totales: undefined,
      internal_error: undefined,
    };
  },
  methods: {
    goToAddTarea() {
      this.root.goToPage("PaginaAgendaAniadirTarea", { value: { "fecha de inicio": this.$utils.getStringFromDate(this.internal_hora) } });
    },
    goToTarea(tarea) {
      this.root.goToPage("PaginaAgendaEditarTarea", { value: tarea });
    },
    goToPreviousDay() {
      const dateTmp = new Date(this.internal_hora);
      dateTmp.setDate(dateTmp.getDate() - 1);
      this.internal_hora = dateTmp;
    },
    goToNextDay() {
      const dateTmp = new Date(this.internal_hora);
      dateTmp.setDate(dateTmp.getDate() + 1);
      this.internal_hora = dateTmp;
    },
    goToPreviousHour() {
      const dateTmp = new Date(this.internal_hora);
      dateTmp.setHours(dateTmp.getHours() - 1);
      this.internal_hora = dateTmp;
    },
    goToNextHour() {
      const dateTmp = new Date(this.internal_hora);
      dateTmp.setHours(dateTmp.getHours() + 1);
      this.internal_hora = dateTmp;
    },
    refreshInternalHoraData(v = this.internal_hora) {
      if (!(v instanceof Date)) {
        this.internal_tareas_de_hora = undefined;
        return;
      }
      // const nowDate = new Date();
      // const nowDateStr = `${this.$utils.padStart(nowDate.getFullYear(), 4, "0")}-${this.$utils.padStart(nowDate.getMonth() + 1, 2, "0")}-${this.$utils.padStart(nowDate.getDate(), 2, "0")} ${this.$utils.padStart(nowDate.getHours(), 2, "0")}`;
      const startDateStr = `${this.$utils.padStart(v.getFullYear(), 4, "0")}-${this.$utils.padStart(v.getMonth() + 1, 2, "0")}-${this.$utils.padStart(v.getDate(), 2, "0")} ${this.$utils.padStart(v.getHours(), 2, "0")}`;
      this.$db
        .select("tarea", {
          filter: (item) => {
            return item["fecha de inicio"].startsWith(startDateStr);
          },
        })
        .then((dataP) => {
          const data = dataP.sort((a, b) => {
            return b["fecha de inicio"] < a["fecha de inicio"] ? 1 : a["fecha de inicio"] < b["fecha de inicio"] ? -1 : b.nombre < a.nombre ? 1 : -1;
          });
          this.internal_tareas_de_hora_fallidas = data.filter((v2) => {
            return !v2["está terminada"];
          });
          this.internal_tareas_de_hora_pendientes = data.filter((v2) => {
            this.$noop(v2);
            // return !v2["está terminada"] && v2["fecha de inicio"] > nowDateStr;
          });
          this.internal_tareas_de_hora_completadas = data.filter((v2) => {
            return v2["está terminada"];
          });
          this.internal_tareas_de_hora_totales = data.length;
          this.internal_tareas_de_hora_objetivos_sumados = data
            .filter((v2) => {
              return v2["está terminada"];
            })
            .reduce(this.$utils.reduceObjetivosFromTarea.bind(this), []);
          this.internal_tareas_de_hora_objetivos_pendientes = data
            .filter((v2) => {
              return !v2["está terminada"];
            })
            .reduce(this.$utils.reduceObjetivosFromTarea.bind(this), []);
          this.internal_tareas_de_hora_objetivos_totales = this.internal_tareas_de_hora_objetivos_sumados.length + this.internal_tareas_de_hora_objetivos_pendientes.length;
          this.internal_tareas_de_hora = data;
          return data;
        })
        .catch((error) => {
          this.internal_error = error;
          throw error;
        });
    },
  },
  watch: {
    internal_hora(v) {
      this.refreshInternalHoraData(v);
    },
  },
  computed: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    this.internal_hora = new Date();
  },
  updated() {},
  destroyed() {},
};
</script>
